import React, { useState, useEffect } from "react"
import { graphql } from "gatsby"
import { unified } from "unified"
import remarkMdx from "remark-mdx"
import rehypeReact from "rehype-react"
import rehypeParse from "rehype-parse"
import Bio from "../components/bio"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Comments from "../components/comments"
import Qna from "../components/qna"
import AttachedImage from "../components/attachedImage"
import { MDXProvider } from "@mdx-js/react"

const BlogPostTemplate = ({ data: { site, mdx: post }, location }) => {
  const siteTitle = site.siteMetadata?.title || `Title`

  const [test, setTest] = useState("")

  useEffect(() => {
    unified()
      .use(rehypeParse, { fragment: true })
      .use(remarkMdx)
      .use(rehypeReact, {
        createElement: (component, props, children) => {
          if (component === "qna") {
            const stringified = props.texts.replace(/\n/g, "")
            const parsed = JSON.parse(stringified)
            return React.createElement(Qna, { texts: parsed }, children)
          } else if (component === "attachedimage") {
            const stringified = props.data.replace(/\n/g, "")
            const parsed = JSON.parse(stringified)
            return React.createElement(
              AttachedImage,
              { data: parsed },
              children
            )
          } else {
            return React.createElement(component, props, children)
          }
        },
        components: {
          p: props => (
            <p
              style={{
                lineHeight: "2",
              }}
            >
              {props.children}
            </p>
          ),
          section: props => (
            <section
              style={{ width: "90%", marginLeft: "auto", marginRight: "auto" }}
            >
              {props.children}
            </section>
          ),
        },
      })
      .process(post.body)
      .then(file => {
        setTest(file.result)
      })
  }, [])

  return (
    <Layout location={location} title={siteTitle}>
      <article
        className="blog-post"
        itemScope
        itemType="http://schema.org/Article"
      >
        <header>
          <h1 itemProp="headline">{post.frontmatter.title}</h1>
          <p>{post.frontmatter.date}</p>
        </header>

        {test}

        <hr />
        <footer>
          <Bio />
        </footer>
      </article>

      <Comments></Comments>
    </Layout>
  )
}

export const Head = ({ data: { mdx: post } }) => {
  return (
    <Seo
      title={post.frontmatter.title}
      description={post.frontmatter.description || post.excerpt}
    />
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($id: String!) {
    site {
      siteMetadata {
        title
      }
    }
    mdx(id: { eq: $id }) {
      id
      excerpt(pruneLength: 160)
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
      }
    }
  }
`
