import * as React from "react"
import styled from "@emotion/styled"

function Qna({ texts }) {
  return (
    <div style={{ background: "rgb(160, 193, 215", padding: "20px 20px" }}>
      {texts.map((text, idx) => {
        const lineBreak = text.replace(/\|\|/g, "\n")
        if (idx % 2 === 0) {
          return <Question key={`answer-${idx}`}>{lineBreak}</Question>
        } else {
          return <Answer key={`question-${idx}`}>{lineBreak}</Answer>
        }
      })}
    </div>
  )
}

export default Qna

const Question = styled.pre`
  background: rgb(255, 233, 65);
  margin-left: 100px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 3px 3px 4px 0px grey;
`

const Answer = styled.pre`
  background: white;
  margin-right: 100px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 3px 3px 4px 0px grey;
`
