import React from "react"

function AttachedImage({ data }) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        marginTop: "20px",
        marginBottom: "20px",
      }}
    >
      <img style={{ maxHeight: "350px" }} src={data[0]}></img>

      <span>{data[1]}</span>
    </div>
  )
}

export default AttachedImage
